import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const KakaoRedirection = () => {
    const navigate = useNavigate();

    useEffect(() => {
        const fetchUserData = async () => {
            try {
                const code = new URL(window.location.href).searchParams.get("code");
                console.log("Authorization Code:", code);  // 추가된 로그

                if (code) {
                    const response = await axios.post(
                        `http://localhost:9000/oauth2/callback/kakao`,
                        new URLSearchParams({
                            grant_type: 'authorization_code',  // 추가
                            client_id: '8bbb8a3533ffe081525698b3125aa35c',        // 추가
                            redirect_uri: 'http://localhost:3000/oauth2/callback/kakao',  // 추가
                            code
                        }),
                        { headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8' } }
                    );
                    console.log("Server Response:", response.data);
                    navigate("/signup/kakao", { state: { userInfo: response.data } });
                } else {
                    console.error("No authorization code found.");
                }
            } catch (error) {
                console.error("Error during Kakao login:", error);
            }
        };

        fetchUserData();
    }, [navigate]);

    return (
        <div>
            <h2>Logging in...</h2>
        </div>
    );
};

export default KakaoRedirection;